	module.exports = {"data":{"content":{"content":"<h2 id=前言>前言</h2>\n<p>async/await 能讓開發者享受異步操作的快感，解決回調地獄的問題。然而，編譯器對於 async/await 的編譯有時候卻不和我們想象中的一樣。透過安裝正確插件，或改回 Promise 版本，我們可以解決這個問題。</p>\n<h2 id=解決方法>解決方法</h2>\n<p>之前在<a href=\"https://calpa.me/2018/03/21/gatsybjs-async-await-upgrade-bug-solution/\">Gatsbyjs async/await 更新報錯解決方法</a> 就已經提及過，使用 babel-plugin-transform-runtime 就好了。</p>\n<pre><code><pre><code class=\"hljs javascript\">npm install --save-dev babel-plugin-transform-runtime\n</code></pre></code></pre>\n<p>然後在 <code>.babelrc</code> 裡面填寫：</p>\n<pre><code><pre><code class=\"hljs javascript\">{\n  <span class=\"hljs-string\">\"plugins\"</span>: [<span class=\"hljs-string\">\"transform-runtime\"</span>]\n}\n</code></pre></code></pre>\n<p>或加入自定義選項：</p>\n<pre><code><pre><code class=\"hljs javascript\">{\n  <span class=\"hljs-string\">\"plugins\"</span>: [\n    [<span class=\"hljs-string\">\"transform-runtime\"</span>, {\n      <span class=\"hljs-string\">\"helpers\"</span>: <span class=\"hljs-literal\">false</span>,\n      <span class=\"hljs-string\">\"polyfill\"</span>: <span class=\"hljs-literal\">false</span>,\n      <span class=\"hljs-string\">\"regenerator\"</span>: <span class=\"hljs-literal\">true</span>,\n      <span class=\"hljs-string\">\"moduleName\"</span>: <span class=\"hljs-string\">\"babel-runtime\"</span>\n    }]\n  ]\n}\n</code></pre></code></pre>\n<p>因為 Babel 會不斷更新，所以具體配置請到官網查閱，或點擊下面的鏈接。</p>\n<p>其實你也可以使用<code>babel-polyfill</code>，不過它會包含 <code>regenerator runtime</code> 及 <code>core-js</code>，打包的體積會變得很大。</p>\n<h2 id=參考資料>參考資料</h2>\n<ol>\n<li><a href=\"https://babeljs.io/docs/en/babel-plugin-transform-runtime#docsNav\">babel-plugin-transform-runtime - Babel</a></li>\n<li><a href=\"https://babeljs.io/docs/en/babel-polyfill/\">babel-polyfill - Babel</a></li>\n</ol>\n","title":"Uncaught ReferenceError: regeneratorRuntime is not defined 解決方法","createdDate":"2018-07-29T20:00+08:00","headerImgur":null,"id":"42YrhxJvlC0Aa2ausM88sY","toc":["前言","解決方法","參考資料"],"tags":"前端, Javascript, async-await"},"latestPosts":{"totalCount":60,"edges":[{"node":{"title":"【網易考拉校招】部門總監面對面-直播答疑邀約（附網易內推碼B222E54）","url":"netease-kaola-live","createdDate":"2018-08-01T20:00+08:00"}},{"node":{"title":"Uncaught ReferenceError: regeneratorRuntime is not defined 解決方法","url":"regenerator-runtime-is-not-defined","createdDate":"2018-07-29T20:00+08:00"}},{"node":{"title":"關於作者","url":"about-calpa-liu","createdDate":"2018-05-01T20:00+08:00"}},{"node":{"title":"【方法】在直接複製貼上內容的時候，可能已經不知不覺注入了一段不可視的「空白」","url":"insert-zero-width-characters","createdDate":"2018-04-06T23:00+08:00"}},{"node":{"title":"知乎無限 live 數據流網站","url":"zhihu-unlimited-lives-website","createdDate":"2018-03-28T20:49+08:00"}},{"node":{"title":"Gatsbyjs async/await 更新報錯解決方法","url":"gatsybjs-async-await-upgrade-bug-solution","createdDate":"2018-03-21T20:00+08:00"}}]}},"pathContext":{"id":"42YrhxJvlC0Aa2ausM88sY"}}

